import React, { Component } from "react";
import Main from "../layout/Main";
import { Link } from "react-router-dom";
import Typed from "react-typed";
import "./temp.scss";
import Emoji from "a11y-react-emoji";

export class ParrotHome extends Component {
  render() {
    return (
      <Main title="Home" description="Harshil Darji">
        <article className="post">
          <header>
            <div className="title">
              <h2 data-testid="heading">Harshil Darji</h2>
              <h4>
                <Typed
                  className="font-weight-light"
                  strings={[
                    "Web Developer",
                    "Cloud Enthusiast",
                    "Linux Admirer",
                    "Open Source Fan",
                    "Gamer",
                  ]}
                  typeSpeed={70}
                  backDelay={400}
                  backSpeed={25}
                  loop
                  cursorChar="|"
                />
              </h4>
            </div>
          </header>
          <p style={{ marginBottom: "2em" }}>
            Welcome to my website. Please feel free to read more{" "}
            <Link to="/about">about me</Link>, or you can check out my{" "}
            <Link to="/resume">resume</Link>,{" "}
            <Link to="/projects">projects</Link>, or{" "}
            <Link to="/contact">contact</Link> me.
          </p>
          <p style={{ marginBottom: "2em" }}>
            Cheers! <Emoji symbol="🧑‍💻" label="tech" />
          </p>
        </article>
        <div className="x">
          <img
            className="y"
            src="https://i.imgur.com/kgianeX.png"
            alt="codepen"
          />
        </div>
        {/* <Carousel repoData={this.state.repoData} /> */}
      </Main>
    );
  }
}

export default ParrotHome;
