import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import data from "../data/contact";

const openLink = (url) => {
  window.open(url, "_blank");
};

const ContactIcons = () => (
  <div className="icons">
    {data.map((s) => (
      <span key={s.label} className="faIcon" onClick={() => openLink(s.link)}>
        <FontAwesomeIcon icon={s.icon} className="icon_color" size="2x" />
      </span>
    ))}
  </div>
);

export default ContactIcons;
