import React from "react";
import routes from "../data/routes";
import { Link } from "react-router-dom";
import Hamburger from "./Template/Hamburger";

function ParrotNavbar({ setDarkMode }) {
  return (
    <>
      <header id="header">
        <nav className="links">
          <ul>
            {routes
              .filter((l) => !l.index)
              .map((l) => (
                <li key={l.label}>
                  <Link to={l.path}>{l.label}</Link>
                </li>
              ))}
          </ul>
        </nav>
        <div className="darkModeSwitch">
          <div className="toggle-btn" id="_1st-toggle-btn">
            <input
              onChange={(e) => setDarkMode(e.target.checked)}
              type="checkbox"
              defaultChecked
            />
            <span></span>
          </div>
        </div>
        <Hamburger setDarkMode={setDarkMode} />
      </header>
    </>
  );
}

export default ParrotNavbar;
